import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'documento-dados',   templateUrl: 'documento_dados.component.html' }) export class DocumentoDadosComponent implements OnInit {       displayMode: DocumentoDadosDisplayModeEnum;     displayModeEnum = DocumentoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = DocumentoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = DocumentoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = DocumentoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum DocumentoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


