import { Routes, RouterModule } from '@angular/router';  import { DocumentoDadosComponent }   from './documento_dados.component'; import { DocumentoDadosDetailsComponent } from './documento_dadosDetails.component'; import { DocumentoDadosEditComponent } from './documento_dadosEdit.component';  const documento_dados_routes: Routes = [   {     path: '',     component: DocumentoDadosComponent,     children: [       { path:'details', component: DocumentoDadosDetailsComponent },       { path:'edit', component: DocumentoDadosEditComponent }     ]   } ];  export const documento_dados_routing = RouterModule.forChild(documento_dados_routes); 


